<!--  -->
<template>
  <div class="ShowDetails">
        <div class="ShowDetails_header">
             <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" >
            <span></span>
            <!-- <span class="moreVideo" @click="toPlayDetailstyle"></span> -->
        </div>
        <div class="hideDiv"></div>
        <div @click.stop class="small_Carousel" v-if="smallSwiper.length>=1">
                <swiper v-if="smallSwiper.length>=1" class="small_swiperWrap" :options="swiperOption">
                    <swiper-slide class="swiperSlide" v-for="(item,index) in smallSwiper" :key="index"> <img :src="item.head_pic"><span>{{item.nickname}}</span> {{showDetailData.username}}{{item.amount}}</swiper-slide>
                </swiper>
        </div>
        <div class="videoBox" >
            <div @click="toplayVideo(showDetailData.videos)" class="mask">
                <img class="playBtn" :src="staticImgH+'bofang.png'"/>
            </div>
            <video width="100%" height="100%"
                :poster="showDetailData.videos+'?x-oss-process=video/snapshot,t_10000,m_fast'">
            </video>
        </div>
        <div class="show">
            <div class="show_title">{{showDetailData.username}}</div>
            <div class="show_text" v-html="showDetailData.content"></div>
        </div>
        <ul class="show_bottom">
            <li>
                <span class="show_bottom_tick">{{showDetailData.votes}}</span>
                <span></span>
            </li>
            <li>
                <span class="show_bottom_tick">{{showDetailData.division_ranking}}</span>
                <span></span>
            </li>
        </ul>
        <div class="show_btn">
            <span class="Dvote" @click="vote"></span>
            <span class="Fshare" @click.stop="shareC">Ta</span>
            <!-- <span class="Svideo" @click="toPlayDetailstyle"></span> -->
        </div>
        <div class="show_btn_hide"></div>
        <!--  -->
            <div class="shareText_wrap" v-if="shareTextShow"  @click="hideShare">
                <img :src="staticImgH+'timg.jpg'"/>
                <div class="shareText">
                    ta
                </div> 
            </div>
        
        <!--  -->
            <div class="vote_wrap" v-if="voteShow" @click.stop="hideVote">
                <div class="vote"  @click.stop>
                    <!--  -->
                    <div class="FreeTicket">
                        <span class="FreeTicket_title">   <span> {{personData.user_votes}} </span></span>
                        <span class="FreeTicket_btn" @click.stop="freeticket"></span>
                    </div>
                    <!--  -->
                    <div class="AssistTicket">
                            <!-- <div class="AssistTicket_title"> <span class="AssistTicket_title_first"></span><span>(10)</span> </div>   -->
                            <div class="AssistTicket_title"> <span class="AssistTicket_title_first"></span></div>  
                            <span class="AssistTicket_text"></span>
                            <div class="SpecialTopicBody_bar">
                                <!--  -->
                                <ul>
                                    <li  :class="barcolorIndex==index?'SpecialTopicBody_barColor':''" @click.stop="getShopDes(item.goods_id,index,item.vote)" v-for="(item,index) in shopList" v-if=" Number(index+1)" :key="index">
                                        <img :src="item.original_img" alt="">
                                        <span @click.stop="toshopdetail(item.goods_id,item.vote)">>></span>
                                    </li>
                                </ul>
                            </div>
                            <div class="AssistTicket_btn" @click.stop="AssistTicket"></div>
                    </div>
                </div>
            </div>
        
        <!--  -->
            <div class="EnterTickets" @click="hideEnterTickets" v-if="EnterTicketsShow">
                <div class="EnterTickets_input" @click.stop>
                    <div class="EnterTickets_inputA">
                        <input type="text" v-model="freeTicNum" @input = "inputing"  placeholder=""/>
                        <span @click.stop="allticNumC"></span>  
                    </div> 
                    <div class="EnterTickets_inputB">:<span>{{personData.user_votes}}</span></div>
                 </div>
                 <div class="EnterTickets_btn" @click.stop="freeInput">
                    
                </div>
            </div>
        <!-- 222222222222222222222222 -->
            <transition name="fade">
                <div class="promptFather" v-if="showPrompt">
                    <div class="prompt" >
                        {{promptContent}}
                    </div>
                </div>
            </transition>
        <!--  -->
            <div class="orderTIshi_wrap" @click="sureOrderTi" v-if="orderTIshiSHow">
                <div class="orderTIshi" @click.stop>
                    <span class="orderTIshi_des">{{showDetailData.username}}{{shopVotes}}</span>
                    <span class="orderTIshi_btn" @click="sureOrderTi"></span>
                </div>
            </div>

            <!--  -->
            <attention/>  
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import wx from 'weixin-jsapi'
import qs from 'qs'
import attention from './../attention/attention'
export default {
    data () {
        return {
            // 
            swiperOption: {//swiper3
                direction: 'vertical',
                observer:true,
                observeParents:true,
                loop:true,
                autoplay: {
                    disableOnInteraction: false,
                },
                speed:300,
            },
            smallSwiper:'',//
            showDetailData:'',//
            shareTextShow:false,  //

            voteShow:false,
            barcolorIndex:-1,
            freeTicNum:1,
            goodId:'',
            EnterTicketsShow:false,
            shopList:'',
            playerInterest:'',
            orderSn:'',
            dataResult:'',
            personData:'',
            orderTIshiSHow:false,

            // 
            promptContent:'', //
            showPrompt:false,//
            timer2:'',
            shopVotes:'',
        };
    },
    
    computed:{
        ...mapState(['apiH','staticImgH','playerId','ShowDetailsPage','abortTime','addressIdIsSel','addressId','Shopvote','playDetailVoteDiv','shopDetatilshow','barcolorIndexShop','playDetailShopDES'])
    },
    components: {
      attention
    },
    mounted(){
        //  
            var shopUrl = window.location.href
            //id
            var shopCan, value;  // 
            shopCan = shopUrl.indexOf("#")  //
            shopUrl=shopUrl.substr(shopCan+1)
            shopCan=shopUrl.indexOf('?')
            if(shopCan > 0){
                shopUrl = shopUrl.substr(shopCan + 1)  //
                var shopArr = shopUrl.split('&') // 
                var shopUrlId = {};//  goodId
                for(var i = 0; i < shopArr.length; i++) {//shopArr
                    shopCan = shopArr[i].indexOf("=");  //=
                    if(shopCan > 0){ //=
                        value = shopArr[i].substring(shopCan + 1); //=     =>value
                        shopCan = shopArr[i].substring(0, shopCan);//=  =key
                        shopUrlId[shopCan] = value;  // key valueshopUrlId
                    }
                }
                if(shopUrlId.player_id){
                    this.playerInterest=shopUrlId.player_id
                }
            }else{
                this.playerInterest=this.playerId
            }

                    //    
                        this.getpaioNUm()
                    //  
                        this.getSmallSwiperData()
                    //  
                        this.toshare()
                    // 
                        this.HelpShopList()
                    // 
                        this.getProgramData()
    },
    methods: {
        // 
                   sureOrderTi(){
                       if(this.ShowDetailsPage){
                            this.orderTIshiSHow=false
                        }else{
                            this.$router.push('/')
                        }
                   },
         inputing(){
            var val=this.freeTicNum.replace(/[^\d]/g,'')
            this.freeTicNum=val
            if(this.freeTicNum>this.personData.user_votes){
                    this.freeTicNum=this.personData.user_votes
            }  
        },
        //
        HelpShopList(){
            this.$http.post('/yuanxiao/goods/goods_list').then((res)=>{
                    if(res.data.code==200){
                        this.shopList=res.data.data.result
                        if(this.Shopvote){
                            this.shopVotes=this.Shopvote
                        }
                    }else{
                        this.toastMsg(res.data.msg)
                    }
            })
        },
        // 
       getSmallSwiperData(){
            var obj=qs.stringify({
                player_id:this.playerInterest
            })
            this.$http.post('/yuanxiao/entering/vote_list',obj).then((res)=>{
                    if(res.data.code==200){
                        this.smallSwiper=res.data.data
                    }else {
                        this.toastMsg(res.data.msg)
                    }
            })
        },
        // 
        getProgramData(){
             var obj=qs.stringify({
                player_id:this.playerInterest
            })
            this.$http.post('/yuanxiao/entering/info',obj).then((res)=>{
                if(res.data.code==200){
                    this.showDetailData=res.data.data
                    //
                        if(this.playDetailVoteDiv=='true'){   //  playDetailVoteDiv 
                            this.voteShow=true    //
                            if(this.addressIdIsSel=='true'){   //addressIdIsSel      ture
                                this.goodId=this.playDetailShopDES.goodsId   
                                this.playerInterest=this.playDetailShopDES.playerId
                                this.barcolorIndex=this.playDetailShopDES.barcolorIndex   //
                                this.AssistTicketTwo()
                            }else if(this.shopDetatilshow=='true'){ //
                                this.goodId=this.shopgoodId
                                this.barcolorIndex=this.barcolorIndexShop
                            }
                        }else if(this.playDetailVoteDiv=='false'){
                            this.voteShow=false  //
                        }
                }else {
                    this.toastMsg(res.data.msg)
                }
            })
        },
         //   
            getpaioNUm(){
                    this.$http.post('/yuanxiao/user/info').then((res)=>{
                        if(res.data.code==200){
                            this.personData=res.data.data
                        }else{
                            this.toastMsg(res.data.msg)
                        }
                    })
            },
        //   
            hideVote(){
                this.voteShow=false
                this.barcolorIndex=-1
            },
        
        // goodid
            getShopDes(goodId,index,vote){
                this.barcolorIndex=index  //
                this.goodId=goodId   
                this.shopVotes=vote
                this.Shopvotes(vote)// 
            },
        //   id
            toshopdetail(goodid,vote){
                this.addressIdIsSels('false') //
                this.shopDetatilshows('true')  // 
                this.playDetailVoteDivs('true')  //true 
                this.barcolorIndexShops(this.barcolorIndex) // 
                this.Shopvotes(vote)// 
                this.shopDetailReturns('/ShowDetails')//
                this.shopgoodIds(goodid)  //gooid
                this.playerIds(this.playerInterest)  //playerID
                this.userIdHInterests(this.playerInterest)  //
                this.$router.push('/shopDetail')  
            },
            //  
            AssistTicket(){
                 var timestamp=new Date().getTime()/1000;
                if(Number(timestamp)>Number(this.abortTime)){
                        this.toastMsg('')
                }else{
                        // goodId
                            if(this.goodId==''){
                                this.toastMsg('')
                            }else{ //
                                    if(this.addressIdIsSel=='false'){   // addressIdIsSelfalse 
                                        this.$http.post("/yuanxiao/user/address_list").then((res)=>{
                                            this.playDetailVoteDivs('true')  //true 
                                            this.playDetailShopDESs({goodsId:this.goodId,playerId:this.playerInterest,barcolorIndex:this.barcolorIndex})  //
                                            if(res.data.data.length){  //
                                                this.ReceiptAddressPages('/ShowDetails')
                                                this.$router.push('/ReceiptAddress')
                                            }else{  //
                                                this.ReceiptAddressAddPages('/ShowDetails')
                                                this.$router.push('/ReceiptAddressAdd')
                                            }
                                        })
                                    }else if(this.addressIdIsSel=='true'){  //
                                            this.AssistTicketTwo()
                                    }
                            }
                }
                
            },
        // 
            AssistTicketTwo(){
                var obj=qs.stringify({
                    goods_id:this.goodId,
                    player_id:this.playerInterest,
                    address_id:this.addressId,
                    prom_type:1,
                })
                this.$http.post('/yuanxiao/goods/goods_buyer',obj).then((res)=>{
                    if(res.data.code==200){
                        this.orderSn=res.data.data.result  //
                        this.getPayPermit()
                    }else{
                        this.toastMsg(res.data.msg)
                    }
                })
            },
        //  wx.config
            getPayPermit(){
                var PayPermitObj=qs.stringify({
                    order_sn:this.orderSn,
                    trade_type:'JSAPI',
                })
                this.$http.post('/yuanxiao/wechat/dopay',PayPermitObj).then((res)=>{
                        if(res.data.code==200){
                            var data=res.data.data
                            this.dataResult=data //
                            var vm=this
                            wx.config({
                                debug: false, // ,apialertpclogpc
                                appId: data.appId, // 
                                timestamp:data.timeStamp, // 
                                nonceStr: data.nonceStr, // 
                                signature: data.sign,// 
                                jsApiList: ['chooseWXPay'] // JS
                            });
                            this.payLe()
                        }else{
                             this.toastMsg(res.data.msg)
                        }
                })
            },
        //  wx.chooseWXPay
            payLe(){
                    this.addressIdIsSels('false') //
                    var vm=this
                    wx.ready(function(){
                        wx.chooseWXPay({
                            timestamp: vm.dataResult.timeStamp, // jssdktimestamptimeStampS
                            nonceStr: vm.dataResult.nonceStr, //  32 
                            package: vm.dataResult.packag, // prepay_idprepay_id=\*\*\*
                            signType: 'MD5', // 'SHA1''MD5'
                            paySign:  vm.dataResult.sign, // 
                            success: function (res) {
                                    vm.addressIdIsSels('false') //
                                    vm.orderTIshiSHow=true
                                    vm.getpaioNUm()
                                    vm.getProgramData()
                                // this.myOrderListPages('/PlayerDetails')  //
                                // vm.orderTypes('WAITSEND')  //orderType
                                // vm.orderNums(2)   //
                                // vm.$router.push('/orderList')  //
                            },
                            fail(){
                                alert('')
                            }
                        });
                    }) 
            },
            // 
            hideEnterTickets(){
                this.EnterTicketsShow=false
            },
             //  
            allticNumC(){
                this.freeTicNum=this.personData.user_votes
            },
            //   
            freeticket(){
                 var timestamp=new Date().getTime()/1000;
                if(timestamp>this.abortTime){
                    this.toastMsg('')
                }else{
                    if(this.personData.user_votes>=1){
                        this.EnterTicketsShow=true
                    }else{
                        this.toastMsg('0')
                    }
                }
            },
             // 
           freeInput(){
               if(this.freeTicNum > this.personData.user_votes){
                   this.freeTicNum=1
                   this.toastMsg('')
                   return
               }
               if(this.freeTicNum <=0){
                   this.freeTicNum=1
                   this.toastMsg('0')
                   return
               }
               var obj=qs.stringify({
                    player_id:this.playerInterest,
                    votes:this.freeTicNum
                })
                this.$http.post('/yuanxiao/entering/spend_vote',obj).then((res)=>{
                    this.EnterTicketsShow=false
                    this.freeTicNum=1
                    if(res.data.code==200){
                        this.getpaioNUm()
                        this.getProgramData()
                        this.toastMsg('')
                    }else{
                        this.toastMsg(res.data.msg)
                    }
                })
           },
         //   
    toshare(){
            var Wobj=qs.stringify({
                player_id:this.playerInterest,
                type:5,
            })
            this.$http.post('/yuanxiao/wechat/get_sign',Wobj).then((res)=>{
                if(res.data.code==200){
                    var data=res.data.data
                    this.test=data.test
                        wx.config({
                            debug: false, // ,apialertpclogpc
                            appId: data.appId, // 
                            timestamp:data.timestamp, // 
                            nonceStr: data.nonceStr, // 
                            signature: data.signature,// 
                            jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // JS
                        });
                        this.toFriend()
                        this.toFriendQuan()
                }else{
                    this.toastMsg(res.data.msg)
                }
        })
    },
    //   
            toFriend(){
                        var vm=this
                        var realLocation=vm.apiH+'/#/ShowDetails?player_id='+vm.playerInterest 
                        wx.ready(function () {   //
                            wx.onMenuShareAppMessage({ 
                                title:'--', // 
                                desc:'', // 
                                link:vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation), // JS
                                imgUrl: vm.apiH+'/static/mock/img/cusKefu.jpg', // 
                                success: function (res) {
                                }
                            })
                        });
            },
        //   
            toFriendQuan(){
                var vm=this
                var realLocation=vm.apiH+'/#/ShowDetails?player_id='+vm.playerInterest 
                wx.ready(function () {   //
                        wx.onMenuShareTimeline({
                                title:'--', // 
                                link: vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation),  // JS
                                imgUrl:vm.apiH+'/static/mock/img/cusKefu.jpg', // 
                                success: function (res) {
                                },
                        })
                });
            },
        //   
            shareC(){
                this.shareTextShow=true
            },
        //   
            hideShare(){
                this.shareTextShow=false
            },
        // 
            toReturn(){
                if(this.ShowDetailsPage){
                    this.$router.push(this.ShowDetailsPage)
                }else{
                    this.$router.push('/')
                }
            },
        // 
            toPlayDetailstyle(){
                this.playerIds(this.playerInterest)   //playerId
                this.$router.push('/Programvideo')
            },
        // 
            vote(){
                this.addressIdIsSels('false')   //  
                this.shopDetatilshows('false')  //
                this.goodId=''  // id
                this.voteShow=true   //
            }, 
        // 
            toplayVideo(itemValue){
                this.$router.push({path:'/videoDetail',query:{videoSrc:itemValue}})
            },
        // 
            toastMsg(msg) {
                let self = this
                clearInterval(self.timer2);
                this.promptContent = msg
                this.showPrompt = true
                self.timer2 = setTimeout(function () {
                self.showPrompt = false
                clearInterval(self.timer2);
                }, 2000)
            },

        ...mapMutations(['playerIds','votePages','barcolorIndexShops','addressIdIsSels','shopDetatilshows','Shopvotes','playDetailVoteDivs','shopDetailReturns','playDetailShopDESs','ReceiptAddressPages','ReceiptAddressAddPages','shopgoodIds','userIdHInterests']),
    }
}
</script>
<style scoped lang="stylus">
.ShowDetails{
    width:100%;
    background :#fff;
}
.ShowDetails_header{
    width:100%;
    height:1.23rem;
    display :flex;
    align-items :center;
    justify-content :center;
    position :fixed;
    top:0;
    left:0;
    z-index:20;
    background:rgba(255, 252, 237, 1);
    font-size:0.48rem;
    color:rgba(0, 0, 0, 1);
    >img{
        width:0.32rem;
        height:0.56rem;
        position :absolute;
        top:50%;
        margin-top:-0.28rem;
        left:0.27rem;
    }
    >.moreVideo{
        height:0.5rem;
        font-size:0.43rem;
        text-align :center;
        line-height :0.5rem;
        position :absolute;
        top:50%;
        margin-top:-0.25rem;
        right:0.27rem;
    }
}
.hideDiv{
  height:1.23rem;
}
// 
    .small_Carousel{
        width:100%;
        height:1.2rem;
        position :fixed;
        top:1.2rem;
        left:0;
        background :rgba(0, 0, 0, 0.5);
        z-index:777;
        >.small_swiperWrap{
            width:100%;
            height:1.173rem;
             .swiperSlide{
                 display :flex;
                 align-items :center;
                 padding: 0 0.32rem;
                 font-size:0.373rem;
                 color:rgba(255, 255, 255, 1);
                 >img{
                     width:0.64rem;
                     height:0.64rem;
                     border-radius:50%;
                     margin-right:0.16rem;
                 }
                 >span{
                     overflow :hidden;
                     text-overflow :ellipsis;
                     white-space:nowrap;
                     font-size:0.32rem;
                     line-height:0.453rem;
                 }
            }
        }
       
    }
.videoBox{
    width:100%;
    height:6.4rem;
    position :relative;
    background :#2f2f2f;
}
.mask{width:100%; height:100%; position:absolute; z-index:10;display:flex;align-items:center;justify-content:center;
      >img{ width:1.6rem; height:1.6rem;}
}
.show{
    padding-bottom:0.32rem;
    border-bottom:0.03rem solid rgba(0, 0, 0, 0.1);
}
.show_title{
    color:rgba(0, 0, 0, 1);
    font-size:0.43rem;
    text-align :center;
    font-size:0.423rem;
    margin-top:0.56rem;
}
.show_text{
    width:100%;
    >>>img{
        width:100%!important;
    }
}
.show_bottom{
    display :flex;
    padding:0.32rem 2rem;
    >li{
        flex:1;
        display :flex;
        flex-direction :column;
        align-items :center;
        font-size: 0.32rem;
        color:rgba(0, 0, 0, 0.5);
        >.show_bottom_tick{
            font-size:0.613rem;
            color:rgba(51, 51, 51, 1);
            margin-bottom:0.32rem;
        }
    }
}
.show_btn{
    width:100%;
    height:1.547rem;
    display :flex;
    align-items :center;
    justify-content :center;
    position :fixed;
    bottom:0;
    left:0;
    background :#fff;
    >span{
        font-size:0.373rem;
        width:3.15rem;
        height:0.72rem;
        background :rgba(65, 40, 76, 1);
        text-align :center;
        line-height :0.72rem;
        color:#fff;
    }
    >.Dvote{
        background :rgba(220, 178, 33, 1);
        border-radius:0.49rem 0 0 0.49rem;
    }
    >.Fshare{
        border-radius:0 0.49rem 0.49rem 0;
    }
    // >.Svideo{
    //     background :rgba(106, 40, 60, 1);
    //     border-radius:0 0.49rem 0.49rem 0;
    // }
}
.show_btn_hide{
    width:100%;
    height:1.547rem;
}
.shareText_wrap{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.9);
    position:fixed;
    top:0;
    left:0;
    z-index:999;
    >img{
        width:2.51rem;
        height:auto;
        position:absolute;
        right:0.32rem;
        top:0.32rem;
    }
    >.shareText{
        width:7.733rem;
        height:5.01rem;
        position:absolute;
        left:0;
        right:0;
        top:0;
        bottom:0;
        margin:auto;
        font-size:0.48rem;
        text-align:center;
        padding:0.4rem;
        line-height:1rem;
        color:#fff;
    }
}

.vote_wrap{
    width:100%;
    height:100%;
    position :fixed;
    top:0;
    left:0;
    z-index:888;
}
.vote{
    width:100%;
    height:50%;
    overflow-y:scroll;
    // height:13.52rem;
    background:rgba(203, 232, 255, 0.9);
    position :fixed;
    bottom:0;
    left:0;
    // 
    >.AssistTicket{
        display:flex;
        flex-direction :column;
        align-items :center;
        padding:0 0.4rem;
        >.AssistTicket_title{
            width:100%;
            color:rgba(0, 0, 0, 1);
            line-height :0.613rem;
            display:flex;
            font-size:0.4rem;
            >span{
                 flex:8.5;
            }
            >.AssistTicket_title_first{
                flex:3.5;
                //  font-weight:550;
            }
            
        }
        >.AssistTicket_text{
            width:100%;
            font-size:0.32rem;
            color:rgba(0, 0, 0, 0.8);
            line-height :0.44rem;
        }
        >.SpecialTopicBody_bar{
            width:100%;
            margin-top:0.27rem;
            >ul{
                // height:4.533rem;
                display :flex;
                // justify-content:space-between;
                flex-wrap:wrap;
                // align-items :center;
                // overflow-x:scroll;
                // display: -webkit-box;
                // -webkit-box-align: center;
                // align-items :center;
                z-index :-1;
                >li{
                    display:flex;
                    flex-direction:column;
                    margin-bottom:0.32rem;
                    margin-right:0.27rem;
                    >img{
                        width:2.67rem;
                        height:1.73rem;
                    }
                    >span{
                        margin-top:0.147rem;
                        color:rgba(0, 0, 0, 0.8);
                        font-size:0.32rem;
                    }
                }
                >.SpecialTopicBody_barColor{
                    border:0.07rem solid #FF5E4D;
                }
            }
        }
        >.AssistTicket_btn{
            width:2.4rem;
            height:0.64rem;
            background :rgba(255, 157, 172, 1);
            font-size:0.347rem;
            color:rgba(255, 255, 255, 1);
            border-radius:2.67rem;
            text-align :center;
            line-height :0.64rem;\
            margin-bottom:0.3rem;
        }
    }
    //
    >.FreeTicket{
        display :flex;
        flex-direction:column;
        margin:0 0.4rem;
        margin-top:0.32rem;
        >.FreeTicket_title{
            font-size:0.427rem;
            color:rgba(0, 0, 0, 1);
            line-height :0.6rem;
            >span{
                color:rgba(252, 64, 93, 1);
            }
        }
        >.FreeTicket_btn{
            width:2.4rem;
            height:0.64rem;
            background:rgba(255, 157, 172, 1);
            font-size:0.347rem;
            color:rgba(255, 255, 255, 1);
            border-radius:2.67rem;
            text-align :center;
            line-height :0.64rem;
            margin:0.17rem auto;
        }
    }
}

.EnterTickets{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.9);
    position:fixed;
    top:0;
    left:0;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    z-index:889;
    >.EnterTickets_input{
        width:7rem;
        height:3rem;
        display:flex;
        flex-direction:column;
        // align-items:center;
        justify-content:center;
        background:#fff;
        border-radius:0.16rem;
        padding:0 0.27rem;
        >.EnterTickets_inputA{
            display:flex;
            align-items:center;
            margin-bottom:0.2rem;
            >input{
                width:5rem;
                height:1rem;
                line-height:0.5rem;
                border:0.02rem solid #ccc;
                outline:none;
                margin-right:0.2rem;
            }
            >span{
                font-size:0.347rem;
                color:rgba(255, 0, 0, 1);
            }
        }
        >.EnterTickets_inputB{
            font-size:0.347rem;
            >span{
                color:rgba(255, 0, 0, 1);
            }
        }
        
    }
    >.EnterTickets_btn{
        width:5.5rem;
        height:1rem;
        border-radius:0.67rem;
        background:rgba(255, 157, 172, 1);
        margin-top:0.53rem;
        color:#fff;
        text-align:center;
        line-height:1rem;
        font-size:0.4rem;
        color:rgba(255, 255, 255, 1);
    }
}

// 
.promptFather{
    width:100%;
    position :absolute;
    bottom:50%;
    left:0;
    z-index:999;
    display :flex;
    justify-content :center;
    align-items :center;
    >.prompt{
        padding:0.15rem 0.3rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.32rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}
// 
.orderTIshi_wrap{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.9);
    position:fixed;
    left:0;
    top:0;
    z-index:999;
   
    >.orderTIshi{
        width:8rem;
        height:3.84rem;
        background:#fff;
        border-radius:0.133rem;
         display:flex;
        flex-direction:column;
        position:absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        margin:auto;
        >.orderTIshi_des{
            width:100%;
            height:3rem;
            font-size:0.373rem;
            color:rgba(0, 0, 0, 1);
            line-height:0.8rem;
            text-align:center;
            padding:0.4rem;
        }
        >.orderTIshi_btn{
            width:100%;
            height:1.253rem;
            font-size:0.373rem;
            color:rgba(255, 57, 36, 1);
            text-align:center;
            line-height:1.253rem;
            border-top:0.03rem solid rgba(204, 204, 204, 0.4);
        }
    }
}
</style>


